"use client";
import React, { useState, useRef, useEffect } from 'react';
import styles from './TaskManager.module.css';
import Header from './Header';

interface Task {
  id: string;
  title: string;
  type: 'habit' | 'daily' | 'todo';
  notes?: string;
  difficulty?: 'easy' | 'medium' | 'hard';
  completed?: boolean;
  isEditing?: boolean;
  startDate?: string;
  repeats?: string;
  repeatEvery?: number;
  repeatOn?: string[];
  tags?: string[];
  dueDate?: string;
  checklist?: string[];
}

interface TaskTypeOption {
  type: 'habit' | 'daily' | 'todo';
  label: string;
  icon: string;
}

type Difficulty = 'easy' | 'medium' | 'hard';

export default function TaskManager() {
  const [activeTab, setActiveTab] = useState<'habits' | 'dailies' | 'todos'>('habits');
  const [tasks, setTasks] = useState<Task[]>([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [isPopupOpen, setIsPopupOpen] = useState(false);
  const [showHabitModal, setShowHabitModal] = useState(false);
  const [showDailyModal, setShowDailyModal] = useState(false);
  const [showTodoModal, setShowTodoModal] = useState(false);
  const [isInitialized, setIsInitialized] = useState(false);
  const [newHabit, setNewHabit] = useState({
    title: '',
    notes: '',
    difficulty: 'easy' as Difficulty
  });
  const [newDaily, setNewDaily] = useState({
    title: '',
    notes: '',
    difficulty: 'easy' as Difficulty,
    startDate: new Date().toISOString().split('T')[0],
    repeats: 'weekly',
    repeatEvery: 1,
    repeatOn: ['Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa', 'Su'],
    tags: [] as string[]
  });
  const [newTodo, setNewTodo] = useState({
    title: '',
    notes: '',
    difficulty: 'easy' as Difficulty,
    checklist: [] as string[],
    dueDate: '',
    tags: [] as string[]
  });
  
  const popupRef = useRef<HTMLDivElement>(null);
  const modalRef = useRef<HTMLDivElement>(null);

  const taskTypes: TaskTypeOption[] = [
    { type: 'habit', label: 'Habit', icon: '‚öñÔ∏è' },
    { type: 'daily', label: 'Daily', icon: 'üìÖ' },
    { type: 'todo', label: 'To Do', icon: '‚úì' },
  ];

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (popupRef.current && !popupRef.current.contains(event.target as Node)) {
        setIsPopupOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  // Initialize tasks from localStorage only once on the client side
  useEffect(() => {
    const savedTasks = localStorage.getItem('tasks');
    if (savedTasks) {
      setTasks(JSON.parse(savedTasks));
    }
    setIsInitialized(true);
  }, []);

  // Save tasks to localStorage whenever they change, but only after initialization
  useEffect(() => {
    if (isInitialized) {
      localStorage.setItem('tasks', JSON.stringify(tasks));
    }
  }, [tasks, isInitialized]);

  const handleCreateHabit = () => {
    if (!newHabit.title.trim()) return;
    
    const habit: Task = {
      id: Math.random().toString(36).substr(2, 9),
      title: newHabit.title,
      notes: newHabit.notes,
      difficulty: newHabit.difficulty,
      type: 'habit',
      completed: false,
    };
    
    setTasks(prevTasks => [...prevTasks, habit]);
    setNewHabit({ title: '', notes: '', difficulty: 'easy' });
    setShowHabitModal(false);
    setActiveTab('habits');
  };

  const handleCreateDaily = () => {
    if (!newDaily.title.trim()) return;
    
    const daily: Task = {
      id: Math.random().toString(36).substr(2, 9),
      title: newDaily.title,
      notes: newDaily.notes,
      difficulty: newDaily.difficulty,
      type: 'daily',
      completed: false,
      startDate: newDaily.startDate,
      repeats: newDaily.repeats,
      repeatEvery: newDaily.repeatEvery,
      repeatOn: newDaily.repeatOn,
      tags: newDaily.tags
    };
    
    setTasks(prevTasks => [...prevTasks, daily]);
    setNewDaily({
      title: '',
      notes: '',
      difficulty: 'easy',
      startDate: new Date().toISOString().split('T')[0],
      repeats: 'weekly',
      repeatEvery: 1,
      repeatOn: ['Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa', 'Su'],
      tags: []
    });
    setShowDailyModal(false);
    setActiveTab('dailies');
  };

  const handleCreateTodo = () => {
    if (!newTodo.title.trim()) return;
    
    const todo: Task = {
      id: Math.random().toString(36).substr(2, 9),
      title: newTodo.title,
      notes: newTodo.notes,
      difficulty: newTodo.difficulty,
      type: 'todo',
      completed: false,
      dueDate: newTodo.dueDate,
      checklist: newTodo.checklist,
      tags: newTodo.tags
    };
    
    setTasks(prevTasks => [...prevTasks, todo]);
    setNewTodo({
      title: '',
      notes: '',
      difficulty: 'easy',
      checklist: [],
      dueDate: '',
      tags: []
    });
    setShowTodoModal(false);
    setActiveTab('todos');
  };

  const updateTaskTitle = (taskId: string, newTitle: string) => {
    setTasks(prevTasks => 
      prevTasks.map(task => 
        task.id === taskId ? { ...task, title: newTitle } : task
      )
    );
  };

  const deleteTask = (taskId: string) => {
    setTasks(prevTasks => prevTasks.filter(task => task.id !== taskId));
  };

  const filteredTasks = tasks.filter(task => {
    const matchesType = activeTab === 'habits' ? task.type === 'habit' :
                       activeTab === 'dailies' ? task.type === 'daily' :
                       task.type === 'todo';
    
    const matchesSearch = task.title.toLowerCase().includes(searchQuery.toLowerCase());
    return matchesType && matchesSearch;
  });

  // Only render the content after initialization to prevent hydration mismatch
  if (!isInitialized) {
    return <div className={styles.loading}>Loading...</div>;
  }

  return (
    <>
      <Header />
      <div className={styles.container}>
        <div className={styles.header}>
          <input
            type="text"
            placeholder="Search"
            className={styles.searchInput}
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
          <div className={styles.addTaskContainer}>
            <button 
              className={styles.addTaskButton}
              onClick={() => setIsPopupOpen(!isPopupOpen)}
            >
              + Add Task
            </button>
            {isPopupOpen && (
              <div className={styles.popup} ref={popupRef}>
                {taskTypes.map((taskType) => (
                  <button
                    key={taskType.type}
                    className={styles.popupOption}
                    onClick={() => {
                      if (taskType.type === 'habit') {
                        setShowHabitModal(true);
                      } else if (taskType.type === 'daily') {
                        setShowDailyModal(true);
                      } else if (taskType.type === 'todo') {
                        setShowTodoModal(true);
                      }
                      setIsPopupOpen(false);
                    }}
                  >
                    <span className={styles.optionIcon}>{taskType.icon}</span>
                    <span>{taskType.label}</span>
                  </button>
                ))}
              </div>
            )}
          </div>
        </div>

        {showHabitModal && (
          <div className={styles.modalOverlay}>
            <div className={styles.modal} ref={modalRef}>
              <div className={styles.modalHeader}>
                <h2>Create Habit</h2>
                <div className={styles.modalActions}>
                  <button 
                    className={styles.cancelButton}
                    onClick={() => setShowHabitModal(false)}
                  >
                    Cancel
                  </button>
                  <button 
                    className={styles.createButton}
                    onClick={handleCreateHabit}
                    disabled={!newHabit.title.trim()}
                  >
                    Create
                  </button>
                </div>
              </div>
              
              <div className={styles.modalContent}>
                <div className={styles.formGroup}>
                  <label>Title*</label>
                  <input
                    type="text"
                    value={newHabit.title}
                    onChange={(e) => setNewHabit({...newHabit, title: e.target.value})}
                    placeholder="Add a title"
                    className={styles.modalInput}
                  />
                </div>

                <div className={styles.formGroup}>
                  <label>Notes</label>
                  <textarea
                    value={newHabit.notes}
                    onChange={(e) => setNewHabit({...newHabit, notes: e.target.value})}
                    placeholder="Add notes"
                    className={styles.modalTextarea}
                    rows={4}
                  />
                </div>

                <div className={styles.habitControls}>
                  <div className={styles.habitButtons}>
                    <button className={styles.positiveButton}>
                      <span>+</span>
                      <span>Positive</span>
                    </button>
                    <button className={styles.negativeButton}>
                      <span>‚àí</span>
                      <span>Negative</span>
                    </button>
                  </div>

                  <div className={styles.formGroup}>
                    <label>Difficulty</label>
                    <select
                      value={newHabit.difficulty}
                      onChange={(e) => setNewHabit({...newHabit, difficulty: e.target.value as 'easy' | 'medium' | 'hard'})}
                      className={styles.modalSelect}
                    >
                      <option value="easy">Easy</option>
                      <option value="medium">Medium</option>
                      <option value="hard">Hard</option>
                    </select>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}

        {showDailyModal && (
          <div className={styles.modalOverlay}>
            <div className={styles.modal} ref={modalRef}>
              <div className={styles.modalHeader}>
                <h2>Create Daily</h2>
                <div className={styles.modalActions}>
                  <button 
                    className={styles.cancelButton}
                    onClick={() => setShowDailyModal(false)}
                  >
                    Cancel
                  </button>
                  <button 
                    className={styles.createButton}
                    onClick={handleCreateDaily}
                    disabled={!newDaily.title.trim()}
                  >
                    Create
                  </button>
                </div>
              </div>
              
              <div className={styles.modalContent}>
                <div className={styles.formGroup}>
                  <label>Title*</label>
                  <input
                    type="text"
                    value={newDaily.title}
                    onChange={(e) => setNewDaily({...newDaily, title: e.target.value})}
                    placeholder="Add a title"
                    className={styles.modalInput}
                  />
                </div>

                <div className={styles.formGroup}>
                  <label>Notes</label>
                  <textarea
                    value={newDaily.notes}
                    onChange={(e) => setNewDaily({...newDaily, notes: e.target.value})}
                    placeholder="Add notes"
                    className={styles.modalTextarea}
                    rows={4}
                  />
                </div>

                <div className={styles.formGroup}>
                  <label>Start Date</label>
                  <input
                    type="date"
                    value={newDaily.startDate}
                    onChange={(e) => setNewDaily({...newDaily, startDate: e.target.value})}
                    className={styles.modalInput}
                  />
                </div>

                <div className={styles.formGroup}>
                  <label>Repeats</label>
                  <select
                    value={newDaily.repeats}
                    onChange={(e) => setNewDaily({...newDaily, repeats: e.target.value})}
                    className={styles.modalSelect}
                  >
                    <option value="daily">Daily</option>
                    <option value="weekly">Weekly</option>
                    <option value="monthly">Monthly</option>
                  </select>
                </div>

                <div className={styles.formGroup}>
                  <label>Repeat Every</label>
                  <div className={styles.repeatEveryContainer}>
                    <input
                      type="number"
                      min="1"
                      value={newDaily.repeatEvery}
                      onChange={(e) => setNewDaily({...newDaily, repeatEvery: parseInt(e.target.value)})}
                      className={styles.modalInput}
                    />
                    <span>Week</span>
                  </div>
                </div>

                <div className={styles.formGroup}>
                  <label>Repeat On</label>
                  <div className={styles.weekDays}>
                    {['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'].map((day) => (
                      <button
                        key={day}
                        className={`${styles.dayButton} ${newDaily.repeatOn.includes(day) ? styles.selected : ''}`}
                        onClick={() => {
                          const newRepeatOn = newDaily.repeatOn.includes(day)
                            ? newDaily.repeatOn.filter(d => d !== day)
                            : [...newDaily.repeatOn, day];
                          setNewDaily({...newDaily, repeatOn: newRepeatOn});
                        }}
                      >
                        {day}
                      </button>
                    ))}
                  </div>
                </div>

                <div className={styles.formGroup}>
                  <label>Difficulty</label>
                  <select
                    value={newDaily.difficulty}
                    onChange={(e) => setNewDaily({...newDaily, difficulty: e.target.value as Difficulty})}
                    className={styles.modalSelect}
                  >
                    <option value="easy">Easy</option>
                    <option value="medium">Medium</option>
                    <option value="hard">Hard</option>
                  </select>
                </div>

                <div className={styles.formGroup}>
                  <label>Tags</label>
                  <input
                    type="text"
                    placeholder="Add tags..."
                    className={styles.modalInput}
                    onKeyDown={(e) => {
                      if (e.key === 'Enter' && e.currentTarget.value.trim()) {
                        setNewDaily({
                          ...newDaily,
                          tags: [...newDaily.tags, e.currentTarget.value.trim()]
                        });
                        e.currentTarget.value = '';
                      }
                    }}
                  />
                </div>
              </div>
            </div>
          </div>
        )}

        {showTodoModal && (
          <div className={styles.modalOverlay}>
            <div className={styles.modal} ref={modalRef}>
              <div className={styles.modalHeader}>
                <h2>Create To Do</h2>
                <div className={styles.modalActions}>
                  <button 
                    className={styles.cancelButton}
                    onClick={() => setShowTodoModal(false)}
                  >
                    Cancel
                  </button>
                  <button 
                    className={styles.createButton}
                    onClick={handleCreateTodo}
                    disabled={!newTodo.title.trim()}
                  >
                    Create
                  </button>
                </div>
              </div>
              
              <div className={styles.modalContent}>
                <div className={styles.formGroup}>
                  <label>Title*</label>
                  <input
                    type="text"
                    value={newTodo.title}
                    onChange={(e) => setNewTodo({...newTodo, title: e.target.value})}
                    placeholder="Add a title"
                    className={styles.modalInput}
                  />
                </div>

                <div className={styles.formGroup}>
                  <label>Notes</label>
                  <textarea
                    value={newTodo.notes}
                    onChange={(e) => setNewTodo({...newTodo, notes: e.target.value})}
                    placeholder="Add notes"
                    className={styles.modalTextarea}
                    rows={4}
                  />
                </div>

                <div className={styles.formGroup}>
                  <label>Checklist</label>
                  <div className={styles.checklistContainer}>
                    <button
                      className={styles.addChecklistItem}
                      onClick={() => {
                        const item = prompt('Enter checklist item:');
                        if (item?.trim()) {
                          setNewTodo({
                            ...newTodo,
                            checklist: [...newTodo.checklist, item.trim()]
                          });
                        }
                      }}
                    >
                      + New checklist item
                    </button>
                    {newTodo.checklist.map((item, index) => (
                      <div key={index} className={styles.checklistItem}>
                        <span>{item}</span>
                        <button
                          onClick={() => {
                            setNewTodo({
                              ...newTodo,
                              checklist: newTodo.checklist.filter((_, i) => i !== index)
                            });
                          }}
                          className={styles.removeChecklistItem}
                        >
                          √ó
                        </button>
                      </div>
                    ))}
                  </div>
                </div>

                <div className={styles.formGroup}>
                  <label>Difficulty</label>
                  <select
                    value={newTodo.difficulty}
                    onChange={(e) => setNewTodo({...newTodo, difficulty: e.target.value as Difficulty})}
                    className={styles.modalSelect}
                  >
                    <option value="easy">Easy</option>
                    <option value="medium">Medium</option>
                    <option value="hard">Hard</option>
                  </select>
                </div>

                <div className={styles.formGroup}>
                  <label>Due Date</label>
                  <input
                    type="date"
                    value={newTodo.dueDate}
                    onChange={(e) => setNewTodo({...newTodo, dueDate: e.target.value})}
                    className={styles.modalInput}
                  />
                </div>

                <div className={styles.formGroup}>
                  <label>Tags</label>
                  <input
                    type="text"
                    placeholder="Add tags..."
                    className={styles.modalInput}
                    onKeyDown={(e) => {
                      if (e.key === 'Enter' && e.currentTarget.value.trim()) {
                        setNewTodo({
                          ...newTodo,
                          tags: [...newTodo.tags, e.currentTarget.value.trim()]
                        });
                        e.currentTarget.value = '';
                      }
                    }}
                  />
                  {newTodo.tags.length > 0 && (
                    <div className={styles.tagList}>
                      {newTodo.tags.map((tag, index) => (
                        <span key={index} className={styles.tag}>
                          {tag}
                          <button
                            onClick={() => {
                              setNewTodo({
                                ...newTodo,
                                tags: newTodo.tags.filter((_, i) => i !== index)
                              });
                            }}
                            className={styles.removeTag}
                          >
                            √ó
                          </button>
                        </span>
                      ))}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        )}

        <div className={styles.tabs}>
          <button
            className={`${styles.tab} ${activeTab === 'habits' ? styles.active : ''}`}
            onClick={() => setActiveTab('habits')}
          >
            Habits
          </button>
          <button
            className={`${styles.tab} ${activeTab === 'dailies' ? styles.active : ''}`}
            onClick={() => setActiveTab('dailies')}
          >
            Dailies
          </button>
          <button
            className={`${styles.tab} ${activeTab === 'todos' ? styles.active : ''}`}
            onClick={() => setActiveTab('todos')}
          >
            To Dos
          </button>
        </div>

        <div className={styles.content}>
          <div className={styles.section}>
            <div className={styles.taskList}>
              {filteredTasks.map(task => (
                <div key={task.id} className={styles.taskItem}>
                  <div className={styles.taskContent}>
                    <input
                      type="text"
                      value={task.title}
                      onChange={(e) => updateTaskTitle(task.id, e.target.value)}
                      placeholder="Enter task title"
                      className={styles.taskInput}
                    />
                    {task.notes && (
                      <div className={styles.taskNotes}>{task.notes}</div>
                    )}
                    {task.type === 'daily' && (
                      <div className={styles.taskDetails}>
                        <div className={styles.taskDetail}>
                          <span>Repeats: {task.repeats}</span>
                          {task.repeatEvery && task.repeats === 'weekly' && (
                            <span> every {task.repeatEvery} week(s)</span>
                          )}
                        </div>
                        {task.repeatOn && task.repeatOn.length > 0 && (
                          <div className={styles.taskDetail}>
                            <span>On: {task.repeatOn.join(', ')}</span>
                          </div>
                        )}
                      </div>
                    )}
                  </div>
                  <div className={styles.taskActions}>
                    <button 
                      className={styles.deleteButton}
                      onClick={() => deleteTask(task.id)}
                    >
                      ‚àí
                    </button>
                  </div>
                </div>
              ))}
              
              {filteredTasks.length === 0 && (
                <div className={styles.emptyState}>
                  <div className={styles.emptyIcon}>
                    {activeTab === 'habits' && '‚öñÔ∏è'}
                    {activeTab === 'dailies' && 'üìÖ'}
                    {activeTab === 'todos' && '‚úì'}
                  </div>
                  <div className={styles.emptyText}>
                    {`These are your ${activeTab}`}
                    <br />
                    {activeTab === 'habits' && "Habits don't have a rigid schedule. You can check them off multiple times per day."}
                    {activeTab === 'dailies' && 'Dailies repeat on a regular basis. Choose the schedule that works best for you!'}
                    {activeTab === 'todos' && 'To Dos need to be completed once. Add checklists to your To Dos to increase their value.'}
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </>
  );
} 